.counter {
  text-align: center;
  font-size: 2.4em;
  color: #ff5722;
  text-shadow: 2px 2px 2px black;
  cursor: pointer;
}

.counter > .number {
  margin: 0.5em;
}

.counter > button {
  width: 120px;
  margin: 5px;
  font-size: 0.7em;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}



