.welcome-app {
  text-align: center;
}

.welcome-app > .header {
  background-color: #222;
  height: 150px;
  padding: 10px;
  color: white;
}

.welcome-app > .header > img.logo {
  -webkit-animation: welcome-app-logo-spin infinite 20s linear;
          animation: welcome-app-logo-spin infinite 20s linear;
  height: 80px;
}

@-webkit-keyframes welcome-app-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes welcome-app-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



